.Main{
    margin:1px 0 0 0;
    background-image: url('../../../images/main-background.png');
    background-size: cover;
    height:100%;
    display: flex;
    justify-content: space-between;
    background-position: center;
    justify-items: center;
    width:100%;
    
    
   
    
}

.LeftSide{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 50%;

  
    
}


.WhatsappLogo{
    margin:0 0 2rem 0;
    padding: 0.4rem;
    img{
        height: 2rem;
        width: 2rem;

        &:hover{
            cursor: pointer;
        }
    }
}

.Texts{
    display: flex;
    flex-direction: column;
    margin:5rem 0 2rem 0;
    
    h2{
        color: white;
        padding: 0 1rem ;
        margin:0;
        
    }
    h3{
        color:white;
        padding: 0 0.5rem;
        margin: 0 ;
        text-decoration: underline;
        text-transform: uppercase;
    }
    p{
        color: white;
        padding: 0.5rem ;
        text-align: justify;
        margin: 6px 0;
        letter-spacing: 1px;
        white-space: 1px;
        font-size:14sp;

    }
   
}

.Icons{
    margin: 12rem 0 0 0;
    display: flex;
    height: inherit;
    width: 100%;
    
    p{  
       writing-mode: vertical-rl;
       text-orientation: sideways;
       margin:0 0.2rem;
       padding: 1rem 0;
       color:white;
       font-size: 14sp;
       

      
    }

    img{
        margin: 3.5rem 0 ;
        padding:1rem 0 0 0;
        height: 100%;
        
    }
    @media (max-width:1200px){
        p{
          
        }
    }
    
}

.RightSide{
    display: flex;
    flex-direction: column;
    height:100%;
    width: 50%;
    justify-content: center;
    justify-items: center;
    

    ul{
        list-style: none;
        display:flex;
        margin:0.2rem 1.2rem;
        justify-content: flex-end;
        img{
            padding:2px;
        }
    }
    
    .Aimage{
        display: flex;
    
        height: 40%;
        padding:1rem;
        justify-content: flex-start;
        margin:4rem 0 0 12rem;
    }

    .RightText{
        display: flex;
        flex-direction: column;
        margin:auto;
        height: 100%;
       
       
        p{
           letter-spacing: 0;
           white-space:30%;
           padding: 2rem;
           margin: auto;
        }

        img{
            height: 50%;
            width: 50%;
            margin:auto;
        }

        a{
            text-decoration: underline;
            color:black;
        }

        a:hover{
            color:gray;
        }

        @media(max-width:1200px){
           

        }


    }

    .ContactUs{
        display: flex;
        flex-direction: column;
        justify-items: center;
        margin-top:10rem;
        height: 100%;
        justify-content: center;
        
        a{
            color:black;
            margin: 8rem 0 0 30rem;
            
        }
        form{ 
           
            margin: 2rem 0 0 18rem;
            input{
                
                margin: 2px 0 0 2px;
                border-radius: 20px;
                border:1px solid lightgray;
                
                &::placeholder{
                    padding: 0.2rem;
                }
                
                &:focus{
                    outline: none;
                }
            }
            button{
                background-color:#61bafa;
                border-radius:20px;
                border: none;
                padding:1px 2px;
                color:black;   
                margin: 0 10px;
                width:20%;
                font-weight:400;
                text-transform: uppercase;

                &:hover{
                    background-color:black;
                    color:white;
                    cursor: pointer;
                }
            }
        }

        @media(max-width:1200px){
           
        }
        
    }

}

