.Header{
    height: 65px;
    width:100%;
    background-color: white;
    box-shadow:0 2px  rgba(0, 0, 0, .1) ;
    margin: 0 auto;
    padding: 0;
    display: flex; 
    align-content: center;
    align-items: center;
    justify-content: space-between;
    justify-items: center;
   
}

.Language{
   padding: 1rem;
   justify-content: space-between;
  
   ul{
     list-style-image: none;
     list-style: none;  
     display: flex;
     padding: 0.4rem;
   }
   li{
     padding: 0.2rem;
   }

   img{
    &:hover{
        cursor: pointer;
    }
}
}

.Logo{
    img{
    align-items: center;
    align-content: center;
    width: 82px;
    height:54px;
    margin: 0 2rem 0 0;
    }

}